<template>
    <div class="cart">
        <div class="left" @click="showFoodList()">
            <!-- 左边车 -->
            <div class="car" :class = "{'current' : totalCount > 0}">
                <i class="icon iconfont icon-gouwuche"></i>
            </div>
            <!-- 商品数量 -->
            <div class="count" v-if = " totalPrice > 0 ">{{totalCount}}</div>
            <!-- 总价格 -->
            <div class="price">
                <!-- <p>{{seller.price}}</p> -->
                <p>¥{{totalPrice}}</p>
            </div>
            <!-- 配送费 -->
            <div class="deliveryPrice">
                <p>另需配送费¥{{seller.deliveryPrice}}元</p>
            </div>
        </div>
        <!-- 去结算 -->
        <div class="right" :class="{'go' : totalPrice >= 20}" @click="goPay">
            <p>{{pay}}</p>
        </div>
        <!-- 购物车清单 -->
        <div class="shopcart-list" v-show="listShow">
            <div class="list-header">
                <h1 class="title">购物车</h1>
                <span class="empty" @click="empty">清空</span>
            </div>
            <div class="list-content" ref="listContent">
                <ul>
                    <li class="shoppingcart-food" v-for="(food, i) in selectFoods" :key="i">
                        <span class="name">{{food.name}}</span>
                        <div class="price"><span>￥{{food.price * food.count}}</span></div>
                        <div class="shoppingcart-wrapper">
                            <shoppingcart :food="food"></shoppingcart>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <!-- 整个黑色背景 -->
        <div class="list-mask" v-show="listShow" @click="hideList()">

        </div>
    </div>
</template>

<script>

import { mapGetters } from 'vuex'
import shoppingcart from '../shoppingcart/shoppingcart'

export default {
    props: ['goods'],
    data () {
        return {
            flag: true
        }
    },
    computed: {
        ...mapGetters([
            'seller'
        ]),
        // 筛选所有有count的商品
        selectFoods () {
            let foods = [];  
            this.goods.forEach( good => {
                good.foods.forEach( food => {
                    if ( food.count > 0 ) {
                        foods.push( food );
                    }
                });
            });
            return foods;
        },
        // 计算购物车商品的总数量 
        totalCount () {
            let count = 0;
            this.selectFoods.forEach( food => {
                count +=food.count;
            });
            return count;   
        },
        // 计算商品总价
        totalPrice () {
            let price = 0;
            this.selectFoods.forEach( food => {
                price += food.price * food.count;
            });
            return price;
        },
        // 结算描述
        pay () {
            if ( this.totalPrice === 0 ) {
                return `￥${this.seller.minPrice}元起送`;
            } else if ( this.totalPrice < this.seller.minPrice ) {
                return `还差 ￥${this.seller.minPrice - this.totalPrice}元` ;  
            } else {
                 return "去结算";   
            }
        },
        // 是否显示
        listShow () {
            if ( !this.totalCount ) {
                this.flag = true;
                return false;
            }
            return !this.flag;
        }
    },
    methods: {
        // 展示购物车列表
        showFoodList () {
            if ( !this.totalCount ) {
                return;
            }
            this.flag = !this.flag;
        },
        // 清空
        empty () {
            this.selectFoods.forEach( (food) => {
                food.count = 0;
            }) ;
        },
        // 隐藏购物车列表
        hideList () {
            this.flag = false;
        },
        // 去结算
        goPay () {
            if ( !this.totalPrice ) {
                return;
            }
            if (this.totalPrice < this.minPrice) {
                return;
            }
            alert("确认支付：" + this.totalPrice + '元');
        }
    },
    components: {
        shoppingcart
    }
}
</script>

<style lang="less" scoped>
    .cart{
        width: 100%;
        height: 1.2rem;
        display: flex;
        .left {
            width: 100% - 3.5rem;
            height: 100%;
            background-color: #141d27;
            .car {
                    position: absolute;
                    bottom: 0.12rem;
                    left: 0.2rem;
                    width: 1.4rem;
                    height: 1.4rem;
                    box-sizing: border-box;
                    border: 0.15rem solid #141d27;
                    border-radius: 50%;
                    background-color: rgba(0,0,0,0.4);
                    &.current {
                        background-color: rgb(0,160,220);
                        i{
                            color: white; 
                        }
                }
                i {
                    position: absolute;
                    bottom: 0.25rem;
                    left: 0.2rem;
                    font-size: 0.72rem;
                    color: rgba(255,255,255,0.4);
                }
            }
            .count {
                    position: relative;
                    top: -0.3rem;
                    left: 1.15rem;
                    width: 0.48rem;
                    height: 0.34rem;
                    border: 1px solid red;
                    border-radius: 0.17rem;
                    color: white;
                    font-size: 0.24rem;
                    text-align: center;
                    line-height: 0.38rem;
                    background-color: red;
                }
            .price {
                position: absolute;
                left: 1.7rem;
                top: -0.01rem;
                font-size: 0.36rem;
                font-weight: 700;
                color: rgba(255,255,255,0.4);
                padding: 0.46rem 0.3rem 0.56rem 0;
                box-sizing: border-box;
            }
            .deliveryPrice {
                position: absolute;
                left: 2.55rem;
                bottom: 0.44rem;
                font-size: 0.26rem;
                color: rgba(255,255,255,0.4);
            }
        }
        .right {
            width: 3.5rem;
            height: 100%;
            font-size: 0.36rem;
            font-weight: 700;
            background-color: rgba(0,0,0,0.8);
            padding: 0.54rem 0.16rem;
            box-sizing: border-box;
            color: rgba(255,255,255,0.4);
            text-align: center;
            line-height: 0.18rem;
            &.go {
                background-color: rgb(0,160,220);                
                color: white;
            }
        }
        .shopcart-list {
            position: absolute;
            left: 0;
            bottom: 1.2rem;
            width: 100%;
            box-sizing: border-box;
            .list-header {
                height: 0.8rem;
                line-height: 0.8rem;
                padding: 0 0.24rem;
                background-color: #f3f5f7;
                border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
                .title {
                    float: left;
                    font-size: 0.28rem;
                    color: rgb(7,17,27);
                }
                .empty {
                    float: right;
                    font-size: 0.28rem;
                    color: rgb(0,160,220);
                }
            }
            .list-content {
                padding: 0 0.18rem;
                max-height: 4rem;
                overflow: hidden;
                background: #fff;
                .shoppingcart-food {
                    position: relative;
                    padding: 0.24rem 0;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .name {
                        line-height: 0.24rem;
                        font-size: 0.24rem;
                        color: rgb(7,17,27);
                    }
                    .price {
                        position: absolute;
                        right: 1.8rem;
                        bottom: 0.26rem;
                        line-height: 0.24rem;
                        font-size: 0.28rem;
                        font-weight: 700;
                        color: rgb(240, 20, 20);
                    }
                    .shoppingcart-wrapper {
                        position: absolute;
                        right: 0;
                        bottom:0.35rem;
                    }
                }
            }
        }
        .list-mask {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
            backdrop-filter: blur(10px);
            opacity: 1;
            background: rgba(7, 17, 27, 0.6);
        }   
    }
</style>



