<template>
  <div class="header">
      <div class="top">
          <div class="top-content">
            <div class="top-content-img">
                <img src="../../../static/imgs/bg.png" alt="" @click="showDetail">      
            </div>
            <div class="top-content-detail">
                <div class="title">
                    <div>
                        <img src="../../../static/imgs/brand.png" alt="">
                    </div>
                    <span>{{seller.name}}</span>
                </div>
                <div class="send">
                    <p>{{seller.description}}/{{seller.deliveryTime}}分钟后到达</p>
                </div>
                <div class="discount">
                    <img src="../../../static/imgs/decrease.png" alt="">
                    <span v-if="seller.supports">{{seller.supports[0].description}}</span>
                </div>
            </div>
            <div class="go">
                <span class="count" v-if="seller.supports">{{seller.supports.length}}个<span class="gogo"> > </span></span>
            </div>               
          </div>
      </div>
      <div class="center">
          <div class="bulletin-img">
              <img src="../../../static/imgs/bulletin.png" alt="">
          </div>
          <div class="bulletin-info">
            <span>{{seller.bulletin}}</span>
          </div>
          <div class="gogogo">
              <span> > </span>
          </div>
        
      </div>
      <div class="bottom">
          <ul>
              <li><router-link to="/Goods">商品</router-link></li>
              <li><router-link to="/Ratings">评价</router-link></li>
              <li><router-link to="/Seller">商家</router-link></li>
          </ul>
      </div>
  </div>
</template>

<script>

import { mapGetters, mapActions} from 'vuex';

export default {
  computed: {
      ...mapGetters ([
        'seller'  
      ])
  },
  methods: {
      ...mapActions([
          'showDetail'
      ]),
  }
}
</script>

<style lang="less" scoped>
    .header {
        width: 100%;
        height: 3.48rem;
        .top {
            width: 100%;
            height: 2.12rem;
            background-color: rgba(7,17,27,0.5);
            padding: 0.48rem 0.24rem 0.36rem 0.48rem;
            box-sizing: border-box;
            .top-content{
                width: 100% - 0.72rem;
                height: 1.28rem;
                display: flex;
                .top-content-img {
                    width: 1.28rem;
                    height: 1.28rem;
                    border-radius: 0.1rem;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                };
                .top-content-detail {
                    margin-left: 0.32rem;
                    width : 4.1rem;
                    height: 1.28rem;
                    .title {
                        margin-top: 0.04rem;
                        width: 100%;
                        height: 0.38rem;
                        img {
                            width: 0.6rem;
                            height: 0.36rem;
                        };
                        span {
                            position: absolute;
                            top: 0.58rem;
                            left: 2.8rem;
                            font-size: 0.32rem;
                            color: white;
                            font-weight: bold;
                            line-height: 0.36rem
                        }
                    };
                    .send {
                        margin-top: 0.18rem;
                        p {
                            font-size: 0.24rem;
                            color: white;
                            font-weight: bold;
                            line-height: 0.24rem;
                        }
                    };
                    .discount {
                        margin-top: 0.2rem;
                        img {
                            width: 0.25rem;
                            height: 0.27rem;
                        };
                        span {
                            position: absolute;
                            top: 1.63rem;
                            left: 2.41rem;
                            font-size: 0.2rem;
                            color: white;
                            font-weight: 200;
                            line-height: 0.24;
                        }
                    }
                };
                .go {
                    background-color: rgba(0,0,0,0.3);
                    padding: 0.14rem 0.16rem 0.14rem 0.3rem;
                    height: 0.48rem;
                    width: 0.9rem;
                    border-radius: 0.4rem;
                    margin-top: 0.6rem;
                    .count {
                        font-size: 0.28rem;
                        color: white;
                        font-weight: 200;
                        line-height: 0.5rem;
                    }
                }
            }
        };
        .center {
            width: 100%;
            height: 0.56rem;
            background-color: rgba(7,17,27,0.2);
            padding: 0 0.24rem 0 0.24rem;
            display: flex;
            line-height: 0.56rem;
            font-size: 0.2rem;
            .bulletin-img img {
                width: 0.45rem;
                height: 0.245rem;
                margin-top: 0.153rem;
                margin-right: 0.08rem;
            };
            .bulletin-info {
                width: 5.8rem;
                overflow: hidden;
            }
        };
        .bottom {
            height: 0.8rem;
            width: 100%;
            background-color: rgba(7,17,27,0.1);
            font-size: 0.28rem;
            border-bottom: 1px solid #ccc;
            ul li {
                display: inline-block;
                padding: 0.26rem 0.9rem;
                color: rgb(77,85,93);
                a {
                    display: block;
                    &.router-link-active {
                        color: red;
                    }
                }
            }
        }
    }
</style>




