<template>
    <div class="shoppingCart">
        <!-- 减少商品 -->
        <div class="minus" @click="minus" :class="{'reduce' : food.count == 0}" v-if='this.food.count'>
            <i class="icon iconfont icon-jianhao"></i>
        </div>
        <!-- 单个商品数量 -->
        <div class="count" :class="{'allNo' : food.count == 0}">
            <!-- {{food.count == 0}} -->
            <div style="width:100%;height:100%">
                {{food.count}}
            </div>
            
        </div>
        <!-- 增加商品数量 -->
        <div class="plus" @click="plus">
            <i class="icon iconfont icon-jiahao"></i>
        </div>
    </div>
</template>

<script>

import Vue from 'vue'

export default {
    props: ['food'],
    methods: {
        plus () {
            if ( !this.food.count ) {
                Vue.set(this.food, 'count', 1);              
            } else {
                this.food.count ++;
            }
        },
        minus () {
            if ( this.food.count ) {
                this.food.count --;
            }
        }
    }
}
</script>

<style lang="less" scoped>
    .shoppingCart {
        position: relative;
        display: flex;
        width: 1.5rem;
        margin-left: 1.5rem;
        i{
            font-size: 0.48rem;
        }
        .plus {
            position: absolute;
            top: -0.3rem;
            right: 0;
            width: 0.5rem;
            color: rgb(0,160,220);
        }
        .minus { 
            position: absolute;
            top: -0.3rem;
            left: 0;    
            width: 0.5rem;
            color: rgb(0,160,220);  
            &.reduce {
                i {
                    visibility: hidden;   
                }
            }                                          
        };
        .count {
            position: absolute;
            top: -0.3rem;
            left: 0.46rem;
            width: 0.5rem;
            text-align: center;
            line-height: 0.48rem;
            font-size: 0.32rem; 
            &.allNo {
                visibility: hidden;
            }                       
        }        
    }
</style>



