<template>
    <div class="goods_detail" v-show="foodshow" ref="goodsDetail">
        <div>
            <div class="top_img">
                <img :src="food.image" alt="" @click="hideGoodsDetail()">
            </div>
            <div class="description">
                <div class="container">
                    <ul>
                        <li class="l1">
                            <span>{{food.name}}</span>
                        </li>
                        <li class="l2">
                            <span class="month">月售{{food.sellCount}}份</span>
                            <span>好评率{{food.rating}}％</span>
                        </li>
                        <li class="l3">
                            <span>¥{{food.price}}</span>
                        </li>
                    </ul>
                    <div class="sCart">
                        
                        <shoppingCart :food='food'></shoppingCart>
                    </div>
                </div>
            </div>
            <div class="trans">

            </div>

            <ratingselect class="ratingSelect"></ratingselect>

            <div class="userRatings">
                <ul>
                    <li v-for="(item, i) in food.ratings" :key="i">
                        <span class="time">{{item.rateTime | time}}</span>
                        <span class="username">{{item.username}}</span>
                        <span class="text">
                            <i class="icon iconfont icon-damuzhion on" v-if="item.rateType == 0"></i>
                            <i class="icon iconfont icon-down down" v-if="item.rateType == 1"></i>
                            <span v-if="item.text">{{item.text}}</span>
                            <span v-if="!item.text">此用户暂无评论</span>
                        </span>
                        <div class="userimg">
                            <img :src="item.avatar" alt="">
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <cart :goods='goods'></cart>
        <!-- <cart></cart> -->
    </div>
</template>

<script>

import BScroll from 'better-scroll'
import { mapGetters } from 'vuex'
import cart from '../cart/cart'
import shoppingCart from '../shoppingcart/shoppingcart'
import ratingselect from '../ratingselect/ratingselect'

export default {
    components: {
        cart,shoppingCart,ratingselect
    },
    computed: {
      ...mapGetters([
          'foodshow',
          'food',
          'goods'
      ])
    },
    watch: {
        food () {
            this.$nextTick( () => {
                if ( !this.menuBScroll ) {
                    this.menuBScroll = new BScroll (this.$refs.goodsDetail, {
                        click:true
                    }); 
                } else {
                    this.menuBScroll.refresh();
                }
            } )
        }
    },
    // methods: {
    //     menuBS () {
    //         let menuBScroll = new BScroll (this.$refs.goodsDetail, {
    //             click:true
    //         });
    //     }
    // },
    filters: {
        time: function (data) {
            var data = new Date(data);
            return data.getFullYear()+"-"+(data.getMonth()+1)+"-"+data.getDate()+" "+data.getHours()+":"+data.getMinutes()+":"+data.getSeconds();
        }
    },
    methods: {
        hideGoodsDetail () {
            this.$store.dispatch('hideGoodsDetail');
        }
    }

}
</script>

<style lang="less" scoped>
    .goods_detail {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: white;
        .top_img {
            position: relative;
            width: 100%;
            height: 0;
            padding-top: 100%;
            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
        .description {
            width: 100%;
            height: 2.16rem;
            padding: 0.36rem;
            box-sizing: border-box;
            border-bottom: 1px solid #ccc;
            .container {
                width: 100%;
                height: 100%;
                .sCart {
                    position: relative;
                    top: -0.3rem;
                    left: 3.5rem;
                }
                .l1 {
                    font-size: 0.28rem;
                    font-weight: 700;
                    color: rgb(7,17,27);
                    line-height: 0.28rem;
                    margin-bottom: 0.16rem;
                }
                .l2 {
                    font-size: 0.2rem;
                    color: rgb(147,153,159);
                    line-height: 0.2rem; 
                    margin-bottom: 0.36rem;
                    .month {
                        margin-right: 0.24rem;
                    }                                       
                }
                .l3 {
                    font-size: 0.36rem;
                    font-weight: normal;
                    color: red;
                    line-height: 0.48rem;
                }
            }
        }
        .trans{
            width: 100%;
            height: 0.38rem;
            background-color: #f3f5f7;
        }
        .introduction {
            width: 100%;
            height: 2.18rem;
            padding: 0.36rem;
            box-sizing: border-box;
            .i-container {
                width: 100%;
                height: 100%;
                .goodsIntro {
                    font-size: 0.3rem;
                    margin-bottom: 0.12rem;
                }
                .intro {
                    font-size: 0.24rem;
                    font-weight: 200;
                    color: rgb(77, 85, 93);
                    line-height: 0.4rem;
                    margin-left: 0.16rem;
                }
            }
        }
        .goods_ratings {
            width: 100%;
            height: 3.2rem;
            padding: 0.36rem;
            box-sizing: border-box;
            border-top: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
            .g-container {
                width: 100%;
                height: 100%;
                .top {
                    width: 100%;
                    height: 1.6rem;
                    padding-bottom: 0.36rem;
                    border-bottom: 1px solid #ccc;
                    box-sizing: border-box;
                    .t-top {
                        width: 100%;
                        height: 0.4rem;
                        font-size: 0.32rem;
                        margin-bottom: 0.24rem;
                    }
                    .t-bottom {
                        ul li {
                            display: inline-block;
                            margin-right: 0.16rem;
                            padding: 0.16rem 0.24rem;
                            margin-right: 0.16rem;
                            border-radius: 0.05rem;
                            &.l11 {
                                background-color: rgb(0, 160, 220);
                                .all {
                                    font-size: 0.24rem;
                                    color: white;
                                    margin-right: 0.08rem;
                                }
                                .allCount {
                                    font-size: 0.2rem;
                                    color: white;
                                }
                            }
                             &.l22 {
                                background-color: #ccecf8;
                                .other {
                                    font-size: 0.24rem;
                                    color: rgb(77,85,93);
                                    margin-right: 0.08rem;
                                }
                                .otherCount {
                                    font-size: 0.2rem;
                                    color: rgb(77,85,93);
                                }
                            }
                             &.l33 {
                                background-color: rgb(77,85,93);
                                .other {
                                    font-size: 0.24rem;
                                    color: white;
                                    margin-right: 0.08rem;
                                }
                                .otherCount {
                                    font-size: 0.2rem;
                                    color: white;
                                }
                            }
                        }
                    }
                }
                .bottom {
                    padding: 0.3rem 0;
                    box-sizing: border-box;
                    i {
                        font-size: 0.48rem;
                        color: rgb(147,153,159);
                        margin-right: 0.1rem;
                        &.current {
                            color: rgb(0,160,220)
                        }
                    }
                    span {
                        color: rgb(147,153,159);
                        font-size: 0.32rem;
                    }
                }
            }
        }
        .userRatings {
            padding: 0.36rem 0.36rem 1.22rem 0.36rem;
            box-sizing: border-box;
            width: 100%;
            height: auto;
            ul li {
                border-bottom: 1px solid #ccc;
                width: 100%;
                height: 1.4rem;
                margin-bottom: 0.32rem;
                .time {
                    font-size: 0.28rem;
                    color: rgb(147,153,159);
                }
                .userimg {
                    width: 0.4rem;
                    height: 0.4rem;
                    position: relative;
                    top: -0.36rem;
                    left: 100% - 8rem;
                    img {
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                    }
                }
                .username {
                    position: relative;
                    left: 2.5rem;
                    top: 0rem;
                    color: rgb(147,153,159);
                    font-size: 0.3rem;
                }
                .text {
                    position: relative;
                    left: -3.6rem;
                    top: 0.7rem;
                    color: rgb(1,17,27);
                    font-size: 0.32rem;
                    .on {
                        color: rgb(0,160,220);
                        font-size: 0.36;
                    }
                    .down {
                        color: rgb(147,153,159);
                        font-size: 0.36; 
                    }
                }
            }
        }
        .cart {
            position: fixed;
            bottom: 0;
            width: 100%;
            height: 1.2rem;
        }
    }
</style>



