<template>
  <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
      <div class="detail" v-show='detailShow'>
          <div class="container">
              <!-- 店名 -->
              <div class="title">
                  <p>{{seller.name}}</p>
              </div>
              <!-- 星星 -->
              <div class="star">
                  <star :score = 'seller.score' :size = "'big'"></star>
              </div>
              <!-- 优惠信息 -->
              <div class="info">
                    <span>优惠信息</span>
              </div>
              <!-- 优惠 -->
              <div class="count">
                  <div class="left">
                      <ul>
                          <li>
                              <img src="../../../static/imgs/decrease.png" alt="">
                          </li>
                          <li>
                              <img src="../../../static/imgs/discount.png" alt="">
                          </li>
                          <li>
                              <img src="../../../static/imgs/discount.png" alt="">
                          </li>
                          <li>
                              <img src="../../../static/imgs/special.png" alt="">
                          </li>
                          <li>
                              <img src="../../../static/imgs/special.png" alt="">
                          </li>
                      </ul>
                  </div>
                  <div class="right">
                      <ul>
                          <li v-for="(item, i) in seller.supports" :key="i">
                              <p>{{item.description}}</p>
                          </li>
                      </ul>
                  </div>
              </div>
              <!-- 商家信息 -->
              <div class="info">
                    <span>商家信息</span>
              </div>
              <!-- 具体信息 -->
              <div class="detail-info">
                  <p>{{seller.bulletin}}</p>
              </div>
              <!-- 返回 -->
              <div class="goBack">
                  <span @click="hideDetail">X</span>
              </div>
          </div>
      </div>
  </transition>
</template>

<script>

import { mapGetters } from 'vuex'
import animate from 'animate.css'
import star from '../star/star.vue'

export default {
    props: ['name'],
    components: {
        star
    },
    computed: {
        ...mapGetters([
            'seller',
            'detailShow'
        ]),
    },
    methods: {
        hideDetail () {
            this.$store.dispatch('hideDetail');
        }
    }
}
</script>

<style lang="less" scoped>
    .detail {
        // font-size: 0.4rem;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background-color: rgba(7,17,27,0.8);
        // filter:blur(10px);
        padding: 1.28rem 0.72rem 0.64rem 0.72rem;
        .container {
            width: 100%;
            height: 100%;
            .title p{
                font-size: 0.36rem;
                font-weight: 700;
                color: white;
                line-height: 0.32rem;
                text-align: center;
                margin-top: 0.1rem;
                margin-bottom: 0.32rem;
            }
            .star {
                text-align: center;
                margin-top: 0.56rem;
            }
            .info {
                width: 100%;
                height: 0.36rem;
                text-align: center;
                margin-top: 0.56rem;  
                margin-bottom: 0.48rem;                                              
                span {
                    font-size: 0.36rem;
                    margin: 0 0.24rem;
                    color: white;
                }
                span::before, span::after {
                    content: '------------------';
                    position: absoluted;
                    top: 0.2rem;
                    left: 0.3rem;
                    width: 2.24rem;
                    height: 0.01rem;
                    color: rgba(255,255,255,0.2);
                }
                span ::after {
                    right: 0;
                }
            }
            .count {
                display: flex;
                img {
                    width: 0.32rem;
                    height: 0.32rem;
                    margin-left: 0.24rem;
                    margin-right: 0.14rem;
                    margin-top: 0.2rem
                }
                .right {
                    padding-top: 0.1rem
                }
                .right ul li {
                    // margin-bottom: 0.1rem;
                    margin-top: 0.2rem;
                    p {
                        font-size: 0.24rem;
                        font-weight: 200;
                        color: white;
                        line-height: 0.3em;
                        height: 0.38rem;
                    }
                }
            }
            .detail-info {
                width: 100%;
                height: auto;
                padding: 0 0.24rem;
                box-sizing: border-box;
                font-size: 0.24rem;
                font-weight: 200;
                line-height: 0.48rem;
                color: white;
            }
            .goBack {
                position: absolute;
                bottom: 0.64rem;
                left: 50% - 0.34rem;
                text-align: center;
                color: rgba(255,255,255,0.3);
                font-size: 0.68rem;
                
            }
        }
    }
</style>



