
<template>
  <div class="goods">
      <div class="allKind" ref="scrollKind">
            <ul>
                <li v-for="(item, i) in goods" :key="i" @click="selectMenu(i)" :class="{'current' : i === currentIndex}">
                    <span class="allKind-item">{{item.name}}</span> 
                </li>
            </ul> 
      </div>
      <div class="detail-kind" ref="scrollDetail">
            <ul>
                <li v-for="(item, k) in goods" :key="k" class="food-list-hook">
                    <ul>
                        <li class="detail-name">
                            <p>{{item.name}}</p>
                        </li>
                        <li v-for="(food, i) in item.foods" :key="i" class="detail-details">
                            <ul>
                                <li>
                                    <img :src="food.icon" alt="">
                                </li>
                                <li class="det">
                                    <p class="p1" @click="showGoodsDetail(food)">{{food.name}}</p>
                                    <p class="p2" v-if="food.description">{{food.description}}</p>
                                    <p class="p3">
                                        <span class="sp1">月售{{food.sellCount}}份</span>
                                        <span class="sp2">好评率{{food.rating}}%</span>
                                    </p>
                                    <p class="p4">
                                        <span  class="sp3">￥</span>
                                        <span  class="sp4">{{food.price}}</span>
                                        <span  class="sp5" v-if="food.oldPrice">￥{{food.oldPrice}}</span>
                                    </p>
                                     <shoppingCart class="sCart" :food = 'food'></shoppingCart>   
                                </li>
                            </ul>
                        </li>
                    </ul>
                </li>
            </ul>
      </div>
      <div class="cart">
          <cart :goods='goods'></cart>
      </div>
      <goodsDetail></goodsDetail>
  </div>
</template>

<script>

import BScroll from 'better-scroll'
import { mapGetters } from 'vuex'
import cart from '../cart/cart.vue'
import goodsDetail from '../goodsdetail/goodsdetail'
import shoppingCart from '../shoppingcart/shoppingcart'

export default {
    data () {
        return {
            listHeight : [],
            scrollY : 0
        }
    },
    created () {
        this.$store.dispatch( 'getGoods' );
        // this.$nextTick( () => {
        //     setTimeout( () => {
        //         this.menuBS();
        //     }, 100);
        // } )
    },
    watch: {
        goods () {
            this.$nextTick( () => {
                setTimeout( () => {
                    this.menuBS();
                    this.calcHeight();
                },300)
                if ( !this.menuBScroll2 ) {
                    this.menuBScroll2 = new BScroll (this.$refs.scrollDetail, {
                        click:true,
                        probeType : 3
                    });
                } else {
                    this.menuBScroll2.refresh();
                }
                if ( !this.menuBScroll ) {
                    this.menuBScroll = new BScroll (this.$refs.scrollKind, {
                        click:true,
                    });
                } else {
                    this.menuBScroll.refresh();
                }
            } )
        }
    },
    components: {
        cart,
        goodsDetail,
        shoppingCart
    },
    computed: {
        ...mapGetters([
            'goods'
        ]),
        // 计算当前索引值
        currentIndex () {
            // 循环listHeight数组
            for (let i = 0; i < this.listHeight.length; i++) {
                let height1 = this.listHeight[i];
                let height2 = this.listHeight[i + 1];
                // 根据滚动的y轴坐标值  与listHeight数组区间进行比较
                if ( this.scrollY >= height1 && this.scrollY < height2 ) {
                    return i;
                }
                // if ( this.scrollY < this.listHeight[i + 1] - 1 ) {
                //     return i
                // }
            }
            return 0;
        },
    },
    methods: {
        menuBS () {
            // let menuBScroll = new BScroll (this.$refs.scrollKind, {
            //     click:true
            // });
            // this.menuBScroll2 = new BScroll (this.$refs.scrollDetail, {
            //     click:true,
            //     probeType : 3
            // });

            // 实时获取y坐标
            this.menuBScroll2.on('scroll', ( pos ) => {
                // console.log(pos.y - 2);
                // this.scrollY = Math.ceil(Math.abs(pos.y - 1));
                // this.scrollY = Math.abs( Math.floor(pos.y - 1) ) ;
                this.scrollY = Math.abs( Math.round(pos.y - 2) );
                
            });
        },

        // 计算li的高度 并且push进listHeight数组当中
        calcHeight () {
            // 获取所有的li
            let foodList = this.$refs.scrollDetail.getElementsByClassName('food-list-hook');
            // 初始值为0
            let height = 0;
            this.listHeight.push( height );
            // 循环所有的li
            for ( let i = 0; i < foodList.length; i++ ) {
                height += foodList[i].clientHeight;
                this.listHeight.push( height );
            };
            console.log( this.listHeight );
        },
        // 左侧点击事件
        selectMenu (i) {
            console.log(i);
            let foodList = this.$refs.scrollDetail.getElementsByClassName('food-list-hook');
            let el = foodList[i];
            this.menuBScroll2.scrollToElement(el, 300);
        },

        // 展示商品的详细信息 
        showGoodsDetail (food) {
            this.$store.dispatch('showGoodsDetail', food);
        }
    }
}
</script>

<style lang="less" scoped>
    .goods {
        width: 100%;
        position: absolute;
        top: 3.48rem;
        bottom: 1.2rem;
        display: flex;
        .allKind{
            width: 1.6rem;
            height: 8.7rem;
            flex: 0 0 1.6rem;
            overflow: hidden;
            // border: 1px solid red;
           ul li {
                width: 1.6rem;
                padding: 0 0.24rem;
                font-size: 0.26rem;
                background-color: #f3f5f7;
                box-sizing: border-box;
                height: 1.08rem;
                // color: rgb(240,20,20);
                display: table;
                &.current {
                    color: red;
                    background-color: #fff;
                }
                .allKind-item {
                    display: table-cell;
                    vertical-align: middle;
                    line-height: 0.28rem;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                }
            } 
        };
        .detail-kind {
            width: 100%-1.6rem;
            overflow: hidden;
            // height: 8.7rem;
            // z-index: -100;
            .detail-name {
                width: 100%;
                height: 0.8rem;
                background-color: #f3f5f7;
                font-size: 0.32rem;
                padding-left: 0.28rem;
                border-left: 3px solid #d9dde1;
                line-height: 0.8rem;
                color: rgb(147,153,159);
            };
            .detail-details{
                padding: 0.36rem;
                box-sizing: border-box;
                border-bottom: 1px solid #f3f5f7; 
                .det {
                    position: relative;
                    top: 0.08rem;
                }
                ul li {
                    display: inline-block;
                    img {
                        width: 1.7rem;
                        height: 1.7rem;
                        margin-right: 0.2rem;
                    };
                    .p1{
                        font-size: 0.3rem;
                        color: rgb(7,17,27);
                        margin-bottom: 0.16rem;
                    };
                    .p2, .sp1, .sp2, .sp5{
                        font-size: 0.22rem;
                        color: rgb(147,153,159); 
                        margin-bottom: 0.16rem; 
                    };
                    .p2 {
                        width: 3rem;
                        height: 0.22rem;
                        overflow: hidden;                                                                      
                    }
                    .sp1 {
                        margin-right: 0.24rem;
                    }
                    .sp3 {
                        color: red;
                        font-size: 0.20rem;
                    }   
                    .sp4 {
                        color:red;
                        font-size: 0.28rem;
                    }
                    .p3 {
                        margin-bottom: 0.16rem;                        
                    };
                }
            }
        }
        .cart {
            position: fixed;
            bottom: 0;
            width: 100%;
            height: 1.2rem;
            z-index: 1;
        }
    }
</style>



