<template>
    <div class="seller" ref="sellers">
        <div>
            <div class="container">
                <div class="top">
                    <div class="left">
                        <ul>
                            <li class="name">
                                {{seller.name}}
                            </li>
                            <li>
                                <star :score = 'seller.serviceScore' :size = "'big'"></star>
                            </li>
                            <li class="l1">
                                <span class="sp1">(661)</span>
                                <span class="month">月售{{seller.sellCount}}单</span>
                            </li>
                        </ul>
                    </div>
                    <div class="right">
                        <ul>
                            <li>
                                <i class="icon iconfont icon-xin"></i>
                            </li>
                            <li>
                                <span class="collection">已收藏</span>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="bottom">
                    <ul>
                        <li class="detail">
                            <ul>
                                <li><div class="start">起送价</div></li>
                                <li><span class="Price">{{seller.minPrice}}</span><span class="yuan">元</span></li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul>
                                <li><div class="start">商家配送</div></li>
                                <li><span class="Price">{{seller.deliveryPrice}}</span><span class="yuan">元</span></li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul>
                                <li><div class="start">平均配送时间</div></li>
                                <li><span class="Price">{{seller.deliveryTime}}</span><span class="yuan">元</span></li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="kong">

            </div>
            <div class="wrap">
                <div class="action">
                    <ul>
                        <li class="title">公告和活动</li>
                        <li class="bulletin">
                            <span>
                                {{seller.bulletin}}
                            </span>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="support">
                <div class="s-container">
                    <ul >
                        <li class="supports" v-for="(item, i) in seller.supports" :key="i">{{item.description}}</li>
                    </ul>
                    <ul class="pics">
                        <li class="l11">
                            <img src="../../../static/imgs/decrease.png" alt="">
                        </li>
                        <li class="l22">
                            <img src="../../../static/imgs/discount.png" alt="">
                        </li>
                        <li class="l33">
                            <img src="../../../static/imgs/discount.png" alt="">
                        </li>
                        <li class="l44">
                            <img src="../../../static/imgs/discount.png" alt="">
                        </li>
                        <li class="l55">
                            <img src="../../../static/imgs/special.png" alt="">
                        </li>
                    </ul>
                    
                </div>
            </div>
            <div class="kong">

            </div>
            <div class="real">
                <div class="real-seller">
                    <ul>
                        <li class="title">商家实景</li>
                        <li class="pic">
                            <div ref="scrollss">
                                <table>
                                    <tr>
                                        <td v-for="(item, i) in seller.pics" :key="i" class="pics">
                                            <img :src="item" alt="">
                                        </td>
                                    </tr>
                                </table>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="kong">

            </div>
            <div class="info">
                <ul>
                    <li class="sellerInfo">
                        <span>商家信息</span>
                    </li>
                    <li v-for="(item,i) in seller.infos " :key="i" class="infos">
                        {{item}}
                    </li>
                </ul>
            </div>
        </div>
        <cart :goods="goods"></cart>
    </div>
</template>

<script>

import BScroll from 'better-scroll'
import { mapGetters } from 'vuex'
import star from '../star/star'
import cart from '../cart/cart'

export default {
    components: {
        star,
        cart
    },
    computed: {
        ...mapGetters([
            'seller',
            'goods'
        ])
    },
    watch: {
        seller () {
            this.$nextTick( () => {
                setTimeout ( () => {
                    this.menuBS();
                    this.menuBS1();
                }, 300)
            } )
        }
    },
    methods: {
        menuBS () {
            let betterScrolls = new BScroll(this.$refs.sellers, {
                click : true
            })
        },
        menuBS1 () {
            let betterScroll = new BScroll(this.$refs.scrollss, {
                click : true,
                scrollX: true
            })
        }
    }
}
</script>

<style lang="less" scoped>
    .seller {
        position: fixed;
        top: 3.48rem;
        bottom: 1rem;
        width: 100%;
        overflow: hidden;
        .container {
            padding:0.36rem;
            box-sizing: border-box;
            height: 3.2rem;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            .top {
                display: flex;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                .left {
                    .name {
                        font-size: 0.36rem;
                        color: rgb(7,17,27);
                        margin-bottom: 0.16rem;
                    }
                    .l1 {
                        position: relative;
                        top: -0.4rem;
                        left: 3.1rem;
                        font-size: 0.24rem;
                        color: rgb(77,85,93);
                        .sp1 {
                            margin-right: 0.2rem;
                        }
                    }
                }
                .right {
                    position: relative;
                    left: 2.8rem;
                    font-size: 0.3rem;
                    color: rgb(77,85,93);
                    i {
                        font-size: 0.8rem; 
                        color: red;                   
                    }
                }
            }
            .bottom {
                .detail {
                    display: inline-block;
                    padding: 0.36rem 0.55rem;
                    box-sizing: border-box;
                    text-align: center;
                    .start {
                        font-size: 0.24rem;
                        color: rgb(147,153,159);
                        margin-bottom: 0.12rem;
                    }
                    .Price{
                        font-size: 0.48rem;
                        font-weight: 200;
                        color: rgb(7,17,27);
                    }
                    .yuan {
                        font-size: 0.24rem;
                        font-weight: 200;
                        color: rgb(7,17,27);
                    }
                }
            }
        }
        .kong {
            width: 100%;
            height: 0.34rem;
            background-color: #f3f5f7;
            border-bottom: 1px solid rgba(7,17,27,0.1); 
            border-top: 1px solid rgba(7,17,27,0.1); 
        }
        .wrap {
            padding: 0.36rem;
            .action{
                border-bottom: 1px solid rgba(7,17,27,0.1);
                padding-bottom: 0.36rem;
                .title {
                    font-size: 0.36rem;
                    margin-bottom: 0.16rem;
                }
                .bulletin {
                    margin-left: 0.24rem;
                    font-size: 0.26rem;
                    line-height: 0.48rem;
                    color: red;
                }
            }
        }
        .support {
            padding: 0.36rem;
            height: 4.5rem;
            .s-container {
                img {
                    width: 0.46rem;
                    margin-left: 0.12rem;
                }
                .supports {
                    width: 100%;
                    padding-bottom: 0.32rem;
                    padding-left: 0.68rem;
                    border-bottom: 1px solid #ccc; 
                    margin-bottom: 0.32rem;
                    font-size: 0.24rem;
                    font-weight: 200;
                    color: rgb(7,17,27);
                }
                .l11 {
                    position: relative;
                    top: -4.57rem;
                    left: 0rem;
                }  
                .l22 {
                    position: relative;
                    top: -4.15rem;
                    left: 0rem;
                } 
                .l33 {
                    position: relative;
                    top: -3.75rem;
                    left: 0rem;
                } 
                .l44 {
                    position: relative;
                    top: -3.3rem;
                    left: 0rem;
                } 
                .l55 {
                    position: relative;
                    top: -2.9rem;
                    left: 0rem;
                } 
            }
        }
        .real {
            padding: 0.36rem 0 0.36rem 0.36rem;
            overflow: hidden;
            .real-seller {
                overflow: hidden;
                .title {
                    font-size: 0.36rem;
                    margin-bottom: 0.16rem;
                }
                .pic {
                    // width: 100%;
                    // height: 1.8rem;
                    overflow: hidden;
                    .pics {
                        // display: inline-block;
                        overflow: hidden;
                        img {
                            width: 2.4rem;
                            height: 1.8rem;
                            margin-right: 0.12rem;
                        }
                    }
                }
            }
        }
        .info {
            padding: 0.36rem;
            .sellerInfo {
                padding-bottom: 0.24rem;
                font-size: 0.36rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
            }
            .infos {
                padding-left: 0.26rem;
                padding-bottom: 0.32rem;
                margin-top: 0.32rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(7,17,27);
                border-bottom: 1px solid rgba(7,17,27,0.1);
                line-height: 0.32rem;
            }
        }
        .cart {
            position: fixed;
            bottom: 0;
            width: 100%;
            height: 1.2rem;
        }
    }
</style>



