import types from '../types.js';
import axios from 'axios'

var state = {
    goods: [],
    food: {},
    foodshow: false
}

var getters = {
    goods ( state ) {
        return state.goods;
    },
    food ( state ) {
        return state.food;
    },
    foodshow ( state ) {
        return state.foodshow;
    },
    
}

var actions = {
    getGoods ( { commit } ) {
        axios.get('./data.json').then( ( { data } ) => {
            console.log(data);
            // if ( data.errno === 0 ) {
                commit( types.GET_GOODS, data.goods);
            // }
        } );
    },
    showGoodsDetail ({ commit, state }, data) {
        commit(types.GET_GOODSDETAIL, data);
    },
    hideGoodsDetail ( {commit} ) {
        commit(types.HIDEGOODSDETAIL)
    }
}

var mutations = {
    [types.GET_GOODS] ( state, data ) {
        state.goods = data;
    },
    [types.GET_GOODSDETAIL] ( state, data ) {
        state.food = data;
        state.foodshow = true;
    },
    [types.HIDEGOODSDETAIL] (state) {
        state.foodshow = false;
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


