<template>
    <div class="ratings" ref="ratingss">
        <div>
            <div class="container">
                <div class="left">
                    <p class="l-p1">{{seller.score}}</p>
                    <p class="l-p2">综合评分</p>
                    <p class="l-p3">高于周边商家{{seller.rankRate}}%</p>
                </div>
                <div class="right">
                    <div class="r-left">
                        <ul>
                            <li>
                                <span>服务态度</span>
                            </li>
                            <li>
                                <span>菜色评分</span>
                            </li>
                            <li>
                                <span>送达时间</span>
                            </li>
                        </ul>
                    </div>
                    <div class="r-center">
                        <ul>
                            <li>
                                <star :score = 'seller.serviceScore' :size = "'small'"></star>
                            </li>
                            <li>
                                <star :score = 'seller.serviceScore' :size = "'small'"></star>
                            </li>
                            <li class="time">
                                <p>{{seller.deliveryTime}}分钟</p>
                            </li>
                        </ul>
                    </div>
                    <div class="r-right">
                        <p>{{seller.serviceScore}}</p>
                        <p>{{seller.foodScore}}</p>
                    </div>
                </div>
            </div>
            <div class="trans">
                
            </div>

            <ratingselect class="ratingSelect"></ratingselect>

            <div class="userRatings">
                <ul>
                    <li v-for="(item, i) in selectRatings" :key="i" class="content">
                        <div class="userImg">
                            <img :src="item.avatar" alt="">
                        </div>
                        <div class="userName">
                            <span>{{item.username}}</span>
                        </div>
                        <div class="time">
                            <span>{{item.rateTime | time}}</span>
                        </div>
                        <div class="star">
                            <star :score = 'item.score' :size = "'small'"></star>                        
                            <span class="arrival" v-if="item.deliveryTime">{{item.deliveryTime}}分钟后到达</span>
                            <span class="arrival" v-if="!item.deliveryTime">暂无送达时间</span>
                        </div>
                        <div class="text">
                            <p v-if="item.text">{{item.text}}</p>
                            <!-- <p v-if="!item.text">用户暂无评论!</p> -->
                        </div>
                        <div class="kind">
                            <i class="icon iconfont icon-damuzhion on" v-if="item.rateType == 0"></i>
                            <i class="icon iconfont icon-down down" v-if="item.rateType == 1"></i>
                            <ul>
                                <li v-for="(kind, i) in item.recommend" :key="i">{{kind}}</li>
                            </ul>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <cart :goods="goods"></cart>
    </div>
</template>

<script>

import BScroll from 'better-scroll'
import { mapGetters } from 'vuex'
import star from '../star/star.vue'
import cart from '../cart/cart'
import ratingselect from '../ratingselect/ratingselect'

export default {
    components: {
        star,
        cart,
        ratingselect
    },
    computed: {
        ...mapGetters([
            'ratings',
            'seller',
            'goods',
            'selectRatings'
        ])
    },
    watch: {
        ratings () {

            this.$nextTick( () => {
                setTimeout( () => {
                    // if( ! this.menuBScroll ){
                    //     this.menuBScroll = new BScroll (this.$refs.ratingss, {});
                    // }else{
                    //     this.menuBScroll.refresh();
                    // }
                    this.menuBS();
                },300)
            } )
        }
    },
    mounted(){
        console.log('mounted')
    },
    methods: {
        menuBS () {
            let menuBScroll = new BScroll (this.$refs.ratingss, {
                click:true
            });
        }
    },
    filters: {
        time : (data) => {
            var data = new Date(data);
            return data.getFullYear()+"-"+(data.getMonth()+1)+"-"+data.getDate()+" "+data.getHours()+":"+data.getMinutes()
        }
    }
}
</script>

<style lang="less" scoped>
    .ratings {
        width: 100%;
        position: absolute;
        top: 3.47rem;
        bottom: 0rem;
        overflow: hidden;
        .container {
            border-top: 1px solid #ccc;            
            padding: 0.36rem 0.48rem;
            box-sizing: border-box;
            width: 100%;
            height: 2rem;
            display: flex;
            border-bottom: 1px solid #ccc;            
            .left {
                width: 2.76rem;
                height: 100%;
                border-right: 1px solid #ccc;
                text-align: center;
                // line-height: 0.4rem;
                .l-p1 {
                    font-size: 0.48rem;
                    color: rgb(255,153,0);
                    line-height: 0.56rem;                    
                }
                .l-p2 {
                    font-size: 0.24rem;
                    color: rgb(2,17,27);
                    line-height: 0.48rem;                    
                }
                .l-p3 {
                    font-size: 0.24rem;
                    color: rgb(2,17,27);
                    line-height: 0.48rem;                    
                }
            }
            .right {
                width: 3.8rem;
                height: 100%;
                display: flex;
                .r-left, .r-right {
                    font-size: 0.24rem;
                    line-height: 0.39rem;
                    margin-top: 0.15rem;
                }
                .r-left {
                    margin-left: 0.48rem
                }
                .r-center {
                    margin: 0.1rem 0.24rem 0 0.24rem;
                    .time {
                        position: relative;
                        top: 0.11rem;
                        left: 0;
                        font-size: 0.24rem;
                        color: rgb(147,153,159);
                    }
                }
                .r-right {
                    color: rgb(255,153,0)
                }
            }
        }
        .trans{
            width: 100%;
            height: 0.38rem;
            background-color: #f3f5f7;
        }
        .userRatings {
            width: 100%;
            padding: 0.36rem 0.36rem 1.2rem 0.36rem;
            box-sizing: border-box; 
            .content {
                width: 100%;
                height: 2.8rem;
                border-bottom: 1px solid #ccc;
                margin-bottom: 0.36rem;
                .userImg {
                    width: 0.56rem;
                    height: 0.56rem;
                    img {
                        border-radius: 50%;
                    }
                }
                .userName {
                    position: relative;
                    top: -0.5rem;
                    left : 1.06rem;
                    font-size: 0.32rem;
                    color: rgb(7,17,27);
                }
                .time {
                    font-size: 0.24rem;
                    color: rgb(147,153,159);
                    font-weight: 200;
                }
                .star{
                    position: relative;
                    top: -0.35rem;
                    left: 0.52rem;
                    .arrival{
                        font-size: 0.24rem;
                        color: rgb(147,153,159);
                        font-weight: 200;
                    }
                }
                .text {
                    position: relative;
                    top : -0.86rem;
                    left: 1rem;
                    width: 80%;
                    line-height: 0.4rem;
                    font-size: 0.26rem;
                    color: rgb(7,17,27);                    
                }
                .kind {
                    position: relative;
                    top: -0.7rem;
                    left: 1rem;
                    width: 80%;
                    height: 0.55rem;
                    overflow: hidden;
                    .on {
                        position: relative;
                        top: 0.1rem;
                        color: rgb(0,160,220);
                        font-size: 0.36rem;
                    }
                    .down {
                        position: relative;
                        top: 0.1rem;
                        color: rgb(147,153,159);
                        font-size: 0.36rem; 
                    }
                    ul li {
                        position: relative;
                        top: -0.34rem;
                        left: 0.5rem;
                        display: inline-block;
                        padding: 0.12rem;
                        box-sizing: border-box;
                        font-size: 0.24rem;
                        color: rgb(147,153,159);
                        border: 1px solid rgba(7,17,27,0.1);
                        border-radius: 0.04rem;
                        background-color: #fff;
                        overflow: hidden;
                        margin: 0 0.1rem;
                        margin-bottom: 0.04rem;
                    }
                }
                .time {
                    position: relative;
                    top: -0.8rem;
                    left: 5rem;
                }
                .arrival {
                    position: relative;
                    top: -0.6rem;
                    left: 2rem;
                }
            }
        }
        .cart {
            position: fixed;
            bottom: 0;
            width: 100%;
            height: 1.2rem;
        }
    }
</style>



