<template>
    <div class="ratingselect">
        <div class="goods_ratings">
            <div class="g-container">
                <div class="top">
                    <div class="t-top">
                        <span>商品评价</span>
                    </div>
                    <div class="t-bottom">
                        <ul>
                            <!-- <li class="l11" :class="{'active': changeTypes === 2}"> -->
                            <li class="l11">
                                <span class="all" @click="select(2)">{{types[2]}}</span><span class="allCount">{{ratings.length}}</span>
                            </li>
                            <!-- <li class="l22" :class="{'active': rateType === 0}"> -->
                            <li class="l22">
                                <span class="other position" @click="select(0)">{{types[0]}}</span><span class="otherCount">{{goodCount}}</span>
                            </li>
                            <!-- <li class="l33" :class="{'active': rateType === 1}"> -->
                            <li class="l33">
                                <span class="other negative" @click="select(1)">{{types[1]}}</span><span class="otherCount">{{badCount}}</span>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="bottom" @click="toggle" >
                    <i class="icon iconfont icon-duihao" ></i>
                    <span>只看有内容的评价</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import { mapGetters } from 'vuex'
import star from '../star/star.vue'

export default {
    components: {
        star
    },
    computed:{
        ...mapGetters([
            'types',
            'ratings',
            'selectRatings'
        ]),
        goodCount(){
            return this.ratings.filter( rating => {
                return rating.rateType === 0; 
            }).length;
        },
        badCount(){
            return this.ratings.filter( rating => {
                return rating.rateType === 1; 
            }).length;
        }
    },
    // 初始化页面
    created(){
        this.$store.dispatch('changeTypes',['推荐','吐槽','全部']);
    },
    methods:{
        select(c){
            this.$store.dispatch('select',c)
        },
        toggle(){
            this.$store.dispatch('toggle');
        }
    }

}
</script>

<style lang="less" scoped>
    .goods_ratings {
        width: 100%;
        height: 3.2rem;
        padding: 0.36rem;
        box-sizing: border-box;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        .g-container {
            width: 100%;
            height: 100%;
            .top {
                width: 100%;
                height: 1.6rem;
                padding-bottom: 0.36rem;
                border-bottom: 1px solid #ccc;
                box-sizing: border-box;
                .t-top {
                    width: 100%;
                    height: 0.4rem;
                    font-size: 0.32rem;
                    margin-bottom: 0.24rem;
                }
                .t-bottom {
                    ul li {
                        display: inline-block;
                        margin-right: 0.16rem;
                        padding: 0.16rem 0.24rem;
                        margin-right: 0.16rem;
                        border-radius: 0.05rem;
                        &.l11 {
                            background-color: red;
                            // background-color: rgb(0, 160, 220, 0.1);
                            &.active {
                                    background-color: rgb(0, 160, 220);
                                }
                            .all {
                                font-size: 0.24rem;
                                color: white;
                                margin-right: 0.08rem;
                                
                            }
                            .allCount {
                                font-size: 0.2rem;
                                color: white;
                            }
                        }
                        &.l22 {
                            background-color: #ccecf8;
                            &.active {
                                    background-color: rgb(0, 160, 220);
                                }
                            .other {
                                font-size: 0.24rem;
                                color: rgb(77,85,93);
                                margin-right: 0.08rem;
                                
                            }
                            .otherCount {
                                font-size: 0.2rem;
                                color: rgb(77,85,93);
                            }
                        }
                        &.l33 {
                            background-color: rgb(77,85,93);
                            &.active {
                                    background-color: rgb(0, 160, 220);
                                }
                            .other {
                                font-size: 0.24rem;
                                color: white;
                                margin-right: 0.08rem;
                                
                            }
                            .otherCount {
                                font-size: 0.2rem;
                                color: white;
                            }
                        }
                    }
                }
            }
            .bottom {
                padding: 0.3rem 0;
                box-sizing: border-box;
                i {
                    font-size: 0.48rem;
                    color: rgb(147,153,159);
                    margin-right: 0.1rem;
                    &.on {
                        color: rgb(0,160,220);
                    }
                }
                span {
                    color: rgb(147,153,159);
                    font-size: 0.32rem;
                }
            }
        }
    }
</style>



